.card {
	padding: 0px;
	margin: 2px;
	border: 1px solid #cecece;
}

.stream_card_lg {
	height: 300px;
	width: 100%;
	border-top: 1px solid #cecece;
	border-right: 0px;
	border-left: 0px;
	border-bottom: 0px;
}
.stream_card_md {
	height: 200px;
	width: 100%;
	border-top: 1px solid #cecece;
	border-right: 0px;
	border-left: 0px;
	border-bottom: 0px;
}
.stream_card_sm {
	height: 150px;
	width: 100%;
	border-top: 1px solid #cecece;
	border-right: 0px;
	border-left: 0px;
	border-bottom: 0px;
}

.stream_card_heading {
	color: #0068b5;
	text-align: center;
	text-transform: capitalize;
	padding: 8px !important;
}

.MuiCardContent-root {
	padding: 0px !important;
}

.videoDiv {
	margin-top: 15px;
}

.reload_button {
	min-width: 50px;
	min-height: 50px;
}
